<template>
  <div>
    <div class="item"
         v-for="(item, index) in list"
         :key="index"
    >
      <div class="item-title border-bottom">
        <span class="item-title-icon" v-if="item.children"></span>
        {{item.title}}
        <div class="item-children-price" v-if="item.price">{{item.price}}</div>
      </div>
      <div class="item-children"
           v-if="item.children">
        <detail-list :list="item.children"></detail-list>
      </div>
    </div>
  </div>
</template>

<script>
  export default {
    name: "DetailList",
    props: {
      list: Array
    }
  }
</script>

<style scoped lang="stylus">
  .item-title
    line-height .8rem
    font-size .32rem
    padding 0 .2rem
  .item-title-icon
    position: relative
    left: .06rem
    top: .06rem
    display: inline-block
    width: .36rem
    height: .36rem
    background: url(http://s.qunarzz.com/piao/image/touch/sight/detail.png) 0 -.45rem no-repeat
    margin-right: .1rem
    background-size: .4rem 3rem
  .item-children
    padding 0 .55rem
    .item-children-price
      float right
      color orange
      font-size .35rem
      font-weight 600
</style>



