<template>
  <div>
    <div class="search">
      <input
        class="search-input" type="text" placeholder="输入城市名或拼音"
        v-model="keyword">
    </div>
    <div class="search-content" ref="search" v-show="hasNoData">
      <ul>
        <li class="search-item border-bottom"
            v-for="item of list"
            :key="item.id"
            @click="handleCityClick(item.name)">
          {{item.name}}
        </li>
        <li class="search-item border-bottom" v-show="!list.length">
          没有找到匹配数据
        </li>
      </ul>
    </div>
  </div>

</template>

<script>
  import Bscroll from 'better-scroll'
  import {mapMutations} from 'vuex'
  export default {
    name: "CitySearch",
    props: {
      cities: Object
    },
    data(){
      return {
        keyword: '',
        list: []
      }
    },
    computed: {
      hasNoData(){
        return this.list.length
      }
    },
    watch: {
      keyword(){
        if(this.timer){
          clearTimeout(this.timer)
        }
        if(!this.keyword){
          this.list = []
          return
        }
        this.timer = setTimeout( () => {
          const result = []
          for(let i in this.cities){
            this.cities[i].forEach( value => {
              if(value.spell.indexOf(this.keyword) > -1 ||
                value.name.indexOf(this.keyword) > -1){
                result.push(value)
              }
            })
          }
          this.list = result
        }, 100)
      }
    },
    mounted(){
      this.scroll = new Bscroll(this.$refs.search)
    },
    methods: {
      handleCityClick(city){
        this.changeCity(city)
        this.$router.push('/')
      },
      ...mapMutations(['changeCity'])
    }
  }
</script>

<style scoped lang="stylus">
  @import '~styles/varibles.styl'
  .search
    height: .72rem
    padding: 0 .1rem
    background-color: $bgColor
    .search-input
      width: 100%
      heigiht: .62rem
      line-height: .62rem
      padding: 0 .1rem
      text-align: center
      border-radius: .06rem
      color: #666
      box-sizing: border-box
  .search-content
    position: absolute
    z-index: 1
    top: 1.58rem
    left: 0
    right: 0
    bottom: 0
    background-color: #eee
    .search-item
      line-height: .62rem
      padding-left: .2rem
      color: #666
      background-color: #fff
</style>



